<template>
    <div class="auction-bid" :class="getClass()">
    <div class="auction-bid-background" :class="getClass()"></div>
        <div class="row-1">
            <div class="column-1">
                <div class="column-title">
                    {{ $localizeFilter('user') }}:
                </div>
                <div class="column-value">
                    <span :style="'font-weight: bold;'">{{ 'ID: ' + bid.clientId }}</span>
                </div>
            </div>

            <div class="column-2">
                <div class="column-title">
                    {{ $localizeFilter('pool_share_size') }}:
                </div>
                <div class="column-value">
                    {{ $utils.formatNumber(amount, 'en-US', 0, 6) }} <span :style="'font-weight: bold;'">USDT</span>
                </div>
            </div>
        </div>

        <div class="row-2">
            <div class="column-3" v-if="bid.type !== 'sell'">
                <div class="column-title">
                    {{ $localizeFilter('price') }}:
                </div>
                <div class="column-value">
                    {{ $utils.formatNumber(payAmount, 'en-US', 0, 6) }} <span :style="'font-weight: bold;'">USDT</span>
                </div>
            </div>

            <div class="column-4" v-if="bid.clientId === profile?.id && bid.type !== 'sell'">
                <button class="change-button" @click="changeBid">
                    {{ $localizeFilter('change') }}
                </button>
                <button class="underline-button" @click="cancelBid">
                    {{ $localizeFilter('cancel') }}
                </button>
            </div>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: ['bid'],
    setup() {
        return {}
    },
    methods: {
        changeBid() {
            this.$emit(
                'changeBid',
                this.bid
            )
        },
        cancelBid() {
            this.$emit(
                'cancelBid',
                this.bid
            )
        },
        getClass() {
            let result = this.bid.type === 'sell' ? 'sell' : 'buy'
            if (this.profile?.id === this.bid.clientId) {
                result = result + '-mine'
            }
            if (this.bid.isPriority) {
                result = result + ' priority'
            }
            return result
        }
    },
    computed: {
        profile() {
            return this.$store.getters.profile
        },
        amount() {
            return (this.bid.amount || 0) / 1000000
        },
        payAmount() {
            return (this.bid.payAmount || 0) / 1000000
        }
    }
})
</script>

