        <div class="input-field">
            <input id="amount" class="custom-input" :placeholder="$localizeFilter('amount_in', ['USDT'])" type="text" v-model.trim="amount"  :class="{ invalid: v$.amount.$error }"/>
            <div class="errors">
                <small class="error" v-for="(error, index) of v$.amount.$errors" :key="index">
