                        <label for="checkbox" class="conditions-label">{{$localizeFilter('modal_sell_bid_agreement')}}</label>
                    </div>
                </div>

        <button class="basic-button submit-button" :class="(conditionsAccepted ? '' : 'inactive') + (submitInProgress ? 'inactive' : '')" @click="submitHandler">{{$localizeFilter('create_an_application')}}</button>
